.popup{
    position: absolute; 
    top: 0; 
    left:0; 
    width:100%; 
    height: 100vh;
    background-color: black;    
    display: flex;
    justify-content: center; 
    align-items: center;
}
.popup-inner{
    position: relative; 
    padding: 32px;
    width: 100%; 
    max-width:640px;
    background-color: #fff;

}
.popup-inner .close-btn{
    position: absolute; 
    top: 16px; 
    right: 16px; 
}